// Imports
import React, { useEffect, useState } from "react";
import Movie from "./components/movie";

// Main API
const API_KEY = "18f9c464cbe935eb9ac38f0d4fc2e1e8";
// API to get Featured movies
const FT_URL =
  "https://api.themoviedb.org/3/discover/movie?sort_by=popularity.desc&api_key=" +
  API_KEY +
  "&page=1";

// API to get search data
const SER_URL =
  "https://api.themoviedb.org/3/search/movie?&api_key=" + API_KEY + "&query=";

// Functional Component
function App() {
  const [movies, setMovies] = useState([]);
  const [term, setTerm] = useState("");
  useEffect(() => {
    fetch(FT_URL)
      .then((res) => res.json())
      .then((data) => {
        console.log(data);
        setMovies(data.results);
      });
  }, []);

  const handleOnSubmit = (e) => {
    e.preventDefault();
    if (term) {
      fetch(SER_URL + term)
        .then((res) => res.json())
        .then((data) => {
          console.log(data);
          setMovies(data.results);
        });
    }
  };
  const handleOnChange = (e) => {
    setTerm(e.target.value);
  };

  const refresh = () => {
    setTerm("");
    fetch(FT_URL)
      .then((res) => res.json())
      .then((data) => {
        console.log(data);
        setMovies(data.results);
      });
  };

  // Return
  return (
    <>
      {/* Header */}
      <header>
        <form onSubmit={handleOnSubmit}>
          <input
            className="search"
            type="search"
            placeholder="Search"
            value={term}
            onChange={handleOnChange}
          />
        </form>
        <button className="clear" onClick={refresh}>
          X
        </button>
      </header>

      {/* Main Container */}
      <div className="movie-container">
        {movies.map((movie) => (
          <Movie key={movie.id} {...movie} />
        ))}
      </div>
    </>
  );
}

// Default Export
export default App;
